<template lang="pug">
.header(:class='{"tab-0": $route.meta.tab === 0}')
  .header-inner-wrapper
    .tabs-wrapper
      .canvas-wrapper(ref="canvas_wrapper")
      svg
        defs
          path(id="path" ":d"="path")
        a(xlink:href="" "@click.prevent"="click_about")
          rect(height="120" width="60" y="0" "x"="50%" ":transform"="`translate(${tabTextOffset[0]-30})`" fill="transparent")
          text(":click"="click" text-anchor="middle")
            textPath(ref="tab_0" xlink:href="#path" ":startOffset"="pathLength/2 + tabTextOffset[0]") About
        a(xlink:href="" "@click.prevent"="click_work")
          rect(height="120" width="60" y="0" "x"="50%" ":transform"="`translate(${tabTextOffset[1]-30})`" fill="transparent")
          text(text-anchor="middle")
            textPath(ref="tab_1" xlink:href="#path" ":startOffset"="pathLength/2 + tabTextOffset[1]") Works
        a(xlink:href="" "@click.prevent"="click_contact")
          rect(height="120" width="60" y="0" "x"="50%" ":transform"="`translate(${tabTextOffset[2]-30})`" fill="transparent")
          text(text-anchor="middle")
            textPath(ref="tab_2" xlink:href="#path" ":startOffset"="pathLength/2 + tabTextOffset[2]") Contact
        path(":d"="underline" fill="transparent" stroke-width="2", :class="{hide: hideUnderline}")
</template>

<script>
import FontFaceObserver from 'fontfaceobserver'
import three from './ThreeDotWave'

function calcLineLength (a, b) {
  return Math.sqrt(((a[0] - b[0]) ** 2) + ((a[1] - b[1]) ** 2))
}

function calcPathLength (arr) {
  let length = 0

  for (let i = 1; i < arr.length; i += 1) {
    length += calcLineLength(arr[i - 1], arr[i])
  }

  return length / window.devicePixelRatio
}

function splitPath (arr, start, to) {
  let length = 0
  let i = 0

  while (length < start) {
    length += calcLineLength(arr[i], arr[i + 1])
    i += 1
  }

  const startIndex = i - 1
  const startExcessLength = length - start

  while (length < to) {
    length += calcLineLength(arr[i], arr[i + 1])
    i += 1
  }

  const endIndex = i - 1
  const endExcessLength = length - to

  const startIndexLineLength = calcLineLength(arr[startIndex], arr[startIndex + 1])
  const startExcessPercent = (startIndexLineLength - startExcessLength) / startIndexLineLength
  arr[startIndex][0] += (arr[startIndex + 1][0] - arr[startIndex][0]) * startExcessPercent
  arr[startIndex][1] += (arr[startIndex + 1][1] - arr[startIndex][1]) * startExcessPercent

  const endIndexLineLength = calcLineLength(arr[endIndex], arr[endIndex + 1])
  const endExcessPercent = (endIndexLineLength - endExcessLength) / endIndexLineLength
  arr[endIndex + 1][0] =
    arr[endIndex][0] +
    ((arr[endIndex + 1][0] - arr[endIndex][0]) *
    endExcessPercent)
  arr[endIndex + 1][1] =
    arr[endIndex][1] +
    ((arr[endIndex + 1][1] - arr[endIndex][1]) *
    endExcessPercent)

  const resultPathArr = arr.slice(startIndex, endIndex + 2)

  return resultPathArr
}

function getSvgPathStr (arr) {
  const ratio = window.devicePixelRatio

  let str = 'M '
  str += `${arr[0][0] / ratio} ${arr[0][1] / ratio}`
  for (let i = 1; i < arr.length; i += 1) {
    str += ` L ${arr[i][0] / ratio} ${arr[i][1] / ratio}`
  }

  return str
}

function LowPassFilter (initValue) {
  this.smoothing = 0.09
  this.oldValue = initValue

  LowPassFilter.prototype.newValue = function update (newValue) {
    let filteredValue = this.oldValue + ((newValue - this.oldValue) * this.smoothing)
    if (Math.abs(filteredValue - newValue) < 0.1) {
      filteredValue = newValue
    }

    this.oldValue = filteredValue
    return filteredValue
  }
}

function getTextWidth (txt, style) {
  const e = document.createElement('span')
  Object.assign(e.style, style)
  e.innerHTML = txt
  document.body.appendChild(e)
  const width = e.offsetWidth
  document.body.removeChild(e)
  return width
}

function calcTabOffset () {
  const bottleneck = Math.min(window.innerWidth, window.innerHeight)
  const tabWidth = bottleneck > 512 ? 512 : bottleneck
  const offset = tabWidth * 0.8 / 2 / 1.3  //eslint-disable-line
  return offset
}

export default {
  name: 'mobile-header',

  mounted () {
    function getSvgTextWidth (elem) {
      const style = {
        fontSize: '13px',
        textTransform: 'uppercase',
        fontFamily: 'FFMark',
        fontWeight: 'bold'
      }

      return getTextWidth(elem.innerHTML, style)
    }

    function setTextWidth () {
      const tabWidth0 = getSvgTextWidth(this.$refs.tab_0)
      const tabWidth1 = getSvgTextWidth(this.$refs.tab_1)
      const tabWidth2 = getSvgTextWidth(this.$refs.tab_2)
      this.tabTextWidth = [tabWidth0, tabWidth1, tabWidth2]
    }

    const font = new FontFaceObserver('FFMark', {
      weight: 'bold'
    })
    font.load().then(() => setTextWidth.call(this))

    const textWidthFilter = new LowPassFilter(this.tabTextWidth[this.tabIndex])
    const offsetFilter = new LowPassFilter(this.tabTextOffset[this.tabIndex])

    three.init(this.$refs.canvas_wrapper, {
      height: 120,
      dotColor: window.innerWidth <= 769 ? 0xffffff : 0x000000,
      fogColor: window.innerWidth <= 769 ? 0x000000 : 0xffffff
    })

    three.onUpdate(() => {
      const offset = calcTabOffset()
      this.tabTextOffset = [-offset, 0, offset]

      const curveArray = three.getCurveArray()
      const svgPathData = getSvgPathStr(curveArray)
      let pathLength = calcPathLength(curveArray)
      this.pathLength = pathLength
      const ratio = window.devicePixelRatio

      let textWidth = this.tabTextWidth[this.tabIndex]
      textWidth = textWidthFilter.newValue(textWidth)

      let underlineOffset = this.tabTextOffset[this.tabIndex]
      underlineOffset = offsetFilter.newValue(underlineOffset)
      pathLength *= ratio
      textWidth *= ratio
      underlineOffset *= ratio

      let underlineArr = splitPath(
        curveArray,
        (pathLength / 2) + underlineOffset - (textWidth / 2), //eslint-disable-line
        (pathLength / 2) + underlineOffset + (textWidth / 2),
      )

      underlineArr = underlineArr.map((point) => {
        point[1] += 10
        return point
      })

      this.underline = getSvgPathStr(underlineArr)
      this.path = svgPathData

      window.addEventListener('resize', this.adaptDotWaveColor)
    })

    this.mql = window.matchMedia('(min-aspect-ratio: 4/3)')
    this.mql.addListener(this.adaptDotWaveColor)
  },
  methods: {
    click () {
      // alert()
    },
    adaptDotWaveColor () {
      if (this.$route.meta.tab === 0) {
        three.changeDotColor(this.mql.matches ? 0x000000 : 0xffffff)
        three.changeFogColor(this.mql.matches ? 0xffffff : 0x000000)
      } else {
        three.changeDotColor(0x000000)
        three.changeFogColor(0xffffff)
      }
    },
    click_about () {
      this.$router.replace(
        {
          name: 'hello'
        }
      )
    },
    click_work () {
      this.$router.replace(
        {
          name: 'works'
        }
      )
    },
    click_contact () {
      this.$router.replace(
        {
          name: 'contact'
        }
      )
    }
  },
  computed: {
    tabIndex () {
      return parseInt(this.$route.meta.tab, 10)
    }
  },
  watch: {
    $route () {
      this.adaptDotWaveColor()
      if (parseInt(this.$route.meta.tab, 10) === this.$route.meta.tab) {
        this.hideUnderline = false
      } else {
        this.hideUnderline = true
      }
    }
  },
  data () {
    let offset
    if (typeof window !== 'undefined') {
      offset = calcTabOffset()
    } else {
      offset = 100
    }

    return {
      path: '',
      underline: '',
      hideUnderline: parseInt(this.$route.meta.tab, 10) !== this.$route.meta.tab,
      pathLength: 0,
      tabTextWidth: [0, 0, 0],
      tabTextOffset: [-offset, 0, offset]
    }
  },
  beforeDestroy () {
    three.destroy()
    this.mql.removeListener(this.adaptDotWaveColor)
  }
}

</script>

<style lang="stylus" scoped>

.header
  padding-top 24px
  height 160px
  position absolute
  width 100%
  top 80px
  z-index 100
  overflow hidden
  &.tab-0
    @media(min-aspect-ratio: 4/3)
      .header-inner-wrapper
        margin-right -10%

.header-inner-wrapper
  position absolute
  margin auto
  margin-right 0
  bottom 0
  top 0
  right 50%
  width 100%
  transition 0.3s ease
  transform translate(50%, 0)
    
.tabs-wrapper
  position relative
  
.canvas-wrapper
  position absolute
  left 0
  top 0

svg
  position absolute
  left 0
  top -32px
  width 100%
  z-index 1000

  text
    font-size 13px
    // letter-spacing 2px
    text-transform uppercase
    font-family "FFMark"
    font-weight bold
    fill black
    transition fill 0.4s ease
  a
    height 100px
    width 100px

  path 
    stroke black
    transition stroke,opacity 0.4s ease
    &.hide
      opacity 0

.header.tab-0
  svg
    text
      fill white
      @media(min-aspect-ratio: 4/3)
        fill black
    path
      stroke white
      @media(min-aspect-ratio: 4/3)
        stroke black
</style>



