<template lang="pug">
#app(
  :class=`{
    "tab-0": $route.meta.tab === 0,
  }`
)
  .logo-wrapper(
    @click="$router.replace({name: 'hello'})"
  )
    .logo
  WebsiteHeader
  transition(:name="transitionName" mode="out-in")
    router-view(:key="$route.path")
  // WebsiteFooter
</template>

<script>
import Header from '@/components/Header'
import Footer from '@/components/Footer'

export default {
  components: {
    WebsiteHeader: Header,
    WebsiteFooter: Footer
  },
  watch: {
    $route (to, from) {
      if (typeof from.meta.tab === 'undefined') return
      this.transitionName = from.meta.tab <= to.meta.tab ? 'out-left' : 'out-right'
    }
  },
  data () {
    return {
      transitionName: 'fade'
    }
  }
}

</script>

<style lang="stylus">
transition-duration= 200ms

*
  box-sizing border-box
  -webkit-tap-highlight-color rgba(0, 0, 0, 0)

body
  font-family  "PingFang SC","PingHei","STHeitiSC-Light", "tahoma", "Myriad Set Pro", "Lucida Grande", "Helvetica Neue", "Helvetica", "Arial", "Verdana", "sans-serif", "microsoft yahei"
  -webkit-font-smoothing antialiased
  -moz-osx-font-smoothing grayscale

body, html
  height 100%
  margin 0
  min-height 560px

@font-face
  font-family 'FFMark'
  src: url('./assets/FFMark-Bold.woff2') format('woff2'),
       url('./assets/FFMark-Bold.woff') format('woff')
  font-weight bold
  font-style normal

@font-face
  font-family 'FFMark'
  src: url('./assets/FFMark-Regular.woff2') format('woff2'),
       url('./assets/FFMarkPro-Book.woff') format('woff2')
  font-weight normal
  font-style normal

@font-face {
  font-family 'NeoSans'
  src url(./assets/neo-sans.woff) format('woff'),
      url(./assets/neo-sans.woff2) format('woff2')
}
  

#app
  height 100%
  display flex
  flex-direction column

.fade-enter-active, .fade-leave-active
  transition all transition-duration ease

.fade-enter, .fade-leave-active
  opacity 0

.out-left-enter-active, .out-left-leave-active
  transition all transition-duration ease-out

.out-left-enter, .out-left-leave-active
  opacity 0

.out-left-enter-active
  transform translate3d(100px,0,0)

.out-left-enter-to
  transform translate3d(0,0,0)

.out-left-leave-active
  transform translate3d(-100px,0,0)




.out-right-enter-active, .out-right-leave-active
  transition all transition-duration ease-out

.out-right-enter, .out-right-leave-active
  opacity 0

.out-right-enter-active
  transform translate3d(-100px,0,0)

.out-right-enter-to
  transform translate3d(0,0,0)

.out-right-leave-active
  transform translate3d(100px,0,0)

</style>


<style lang='stylus' scoped>
transition-duration= 200ms

.logo-wrapper
  height 80px
  width 80px
  position absolute
  top 10px
  z-index 10000
  cursor pointer
  left 50%
  margin-left -40px
  .logo
    height 80px
    width 80px
    background-image url("./assets/wz_avatar.png")
    background-position center
    background-repeat no-repeat
    background-size auto 100%
    transition background-image transition-duration ease

  @media(min-aspect-ratio: 4/3)
    left 0
    margin-left 10vh
    top 72px

  &.tab-0
    .logo
      background-image url("./assets/wz_avatar_white.png")
      @media(min-aspect-ratio: 4/3)
        position fixed

#app
  &.tab-0
    .logo-wrapper .logo
      background-image url("./assets/wz_avatar_white.png")
      @media(min-aspect-ratio: 4/3)
        position fixed
</style>


