<template lang="pug">
.main
  .bg.bg-1
  .bg.bg-2
  .content
    .textrect-wrapper(:style="{transform: `translate(${-rotateY*2}px,${rotateX*2}px) rotateX(${rotateX}deg) rotateY(${rotateY}deg)`}")
      .textrect(@mouseleave="handleTextFold(-1, $event)" ref="textrect")
        .row.hello(:style="calcRowStyle(0)")
          .title-wrapper(
            :style="{transform: `scale(${titleScale[0]})`}"
            @click     ="handleTextFold(0, $event)"
            @mouseenter="handleTextFold(0, $event)"
            @mouseleave="handleTextFold(0, $event)"
          )
            .text.color Hello
          transition(name="zoom-fade")
            .blank(
              v-show="foldOffsetEnable[1]"
              ref="blank.0"
              :class="{'calculating-hide': isCalculatingFoldOffset}"
            )
              | How did you get here?<br>I'm waiting for you so long!
        .row.wz(:style="calcRowStyle(1)")
          .title-wrapper(
            :style="{transform: `scale(${titleScale[1]})`}"
            @click     ="handleTextFold(1, $event)"
            @mouseenter="handleTextFold(1, $event)"
            @mouseleave="handleTextFold(1, $event)"
          )
            .text.small This is
            .text.color &nbsp;Zhao Wang
          transition(name="zoom-fade")
            .blank(
              v-show="foldOffsetEnable[2]"
              ref="blank.1"
              :class="{'calculating-hide': isCalculatingFoldOffset}"
            )
              | Finding passion and curiosity about Tech / Art / Music / LifeStyle, which has become a bit of an obsession of mine. If you are the same, let's making some different :)
        .row.designer(:style="calcRowStyle(2)")
          .title-wrapper(
            :style="{transform: `scale(${titleScale[2]})`}"
            @click     ="handleTextFold(2, $event)"
            @mouseenter="handleTextFold(2, $event)"
            @mouseleave="handleTextFold(2, $event)"
          )
            .text.small A
            .text.color &nbsp;designer
          transition(name="zoom-fade")
            .blank(
              v-show="foldOffsetEnable[3]"
              ref="blank.2"
              :class="{'calculating-hide': isCalculatingFoldOffset}"
            )
              | Experience in UI / UX / Graphic Design and keen interested in Interior / Industrial Design.
        .row.developer(:style="calcRowStyle(3)")
          .title-wrapper(
            :style="{transform: `scale(${titleScale[3]})`}"
            @click     ="handleTextFold(3, $event)"
            @mouseenter="handleTextFold(3, $event)"
            @mouseleave="handleTextFold(3, $event)"
          )
            .text.small and
            .text.color &nbsp;Developer
          transition(name="zoom-fade")
            .blank(
              v-show="foldOffsetEnable[4]"
              ref="blank.3"
              :class="{'calculating-hide': isCalculatingFoldOffset}"
            )
              | Experience in Front-end dev, and learning about realtime rendering.
        .row.shanghai(:style="calcRowStyle(4)")
          .title-wrapper(
            :style="{transform: `scale(${titleScale[4]})`}"
            @click     ="handleTextFold(4, $event)"
            @mouseenter="handleTextFold(4, $event)"
            @mouseleave="handleTextFold(4, $event)"
          )
            .text.small based in
            .text.color &nbsp;Shanghai
          transition(name="zoom-fade")
            .blank(
              v-show="foldOffsetEnable[5]"
              ref="blank.4"
              :class="{'calculating-hide': isCalculatingFoldOffset}"
            )
              | No comment
        .row.contact(:style="calcRowStyle(5)")
          .title-wrapper(
            :style="{transform: `scale(${titleScale[5]})`}"
            @click     ="handleTextFold(5, $event)"
            @mouseenter="handleTextFold(5, $event)"
            @mouseleave="handleTextFold(5, $event)"
          )
            .text.small Feel free to
            .text.color &nbsp;Contact
          transition(name="zoom-fade")
            .blank(
              v-show="foldOffsetEnable[6]"
              ref="blank.5"
              @click="$router.replace({name: 'contact'})"
              :class="{'calculating-hide': isCalculatingFoldOffset}"
            )
</template>

<script>
import TWEEN from 'tween.js'
import FontFaceObserver from 'fontfaceobserver'

export default {
  name: 'hello',
  components: {
  },
  mounted () {
    document.addEventListener('mousemove', this.mousemove)

    this.textRectRotate = {
      rotateX: this.rotateX,
      rotateY: this.rotateY
    }
    this.textRectRotateTween = new TWEEN.Tween(this.textRectRotate)
    this.textRectRotateTween.easing(TWEEN.Easing.Quadratic.Out)

    this.foldTween = new TWEEN.Tween(this.foldOffsetValue.slice())
    this.foldTween.easing(TWEEN.Easing.Cubic.Out)

    this.scaleTween = new TWEEN.Tween(this.titleScale.slice())
    this.scaleTween.easing(TWEEN.Easing.Cubic.Out)

    function animate () {
      requestAnimationFrame(animate)
      TWEEN.update()
    }

    animate()

    window.addEventListener('resize', this.calcFoldOffsetHeight)

    const font = new FontFaceObserver('FFMark', {
      weight: 'bold'
    })
    font.load().then(() => this.calcFoldOffsetHeight())
  },
  beforeDestroy () {
    document.removeEventListener('mousemove', this.mousemove)
    window.removeEventListener('resize', this.calcFoldOffsetHeight)
  },
  methods: {
    calcRowStyle (i) {
      return {
        transform: `translate(0,${this.foldOffsetValue[i]}em)`
      }
    },
    calcFoldOffsetHeight () {
      if (this.perviousWidth === window.innerWidth) return

      this.perviousWidth = window.innerWidth
      this.isCalculatingFoldOffset = true

      if (this.foldOffsetEnable.filter(val => !val).length > 0) {
        this.foldOffsetEnable = this.foldOffsetEnable.map(() => true)
      }

      this.$nextTick(() => {
        const foldOffsetScale = (32 / this.$refs.textrect.offsetWidth) * 0.9

        Object.keys(this.$refs)
          .filter(key => /blank\.\d/.test(key))
          .forEach((key, i) => {
            this.foldOffset[i + 1] = (this.$refs[key].offsetHeight * foldOffsetScale) + 3.4
          })

        this.foldOffsetEnable = this.foldOffsetEnable.map(() => false)
        this.$nextTick(() => {
          this.isCalculatingFoldOffset = false
        })
      })
    },
    mousemove (e) {
      if (window.innerWidth < 480) {
        if (this.rotateX === 0 && this.rotateY === 0) {
          return
        }

        this.rotateX = this.textRectRotate.rotateX
        this.rotateY = this.textRectRotate.rotateY

        return
      }

      const rotateY = (e.clientX - (window.innerWidth / 2))
                    / (window.innerWidth / 2)* 0.03 * 90 //eslint-disable-line
      const rotateX = -(e.clientY - (window.innerHeight / 2))
                    / (window.innerHeight / 2) * 0.03 * 90 //eslint-disable-line

      this.textRectRotateTween
        .stop()
        .to({
          rotateX,
          rotateY
        }, 200)
        .onUpdate(() => {
          this.rotateX = this.textRectRotate.rotateX
          this.rotateY = this.textRectRotate.rotateY
        })
        .start()
    },
    handleTextFold (i, e) {
      const index = i + 1
      const self = this

      function scaleTween (scale) {
        this.scaleTween
          .to(scale, 600)
          .onUpdate(function update () {
            self.$forceUpdate()
            self.titleScale = this
          })
          .start()
      }

      function foldTween () {
        const targetValue = this.foldOffsetEnable
          .reduce((prev, curr, folderIndex) => {
            let sumOffset = 0
            for (let j = 0; j <= folderIndex; j += 1) {
              if (this.foldOffsetEnable[j]) {
                sumOffset += this.foldOffset[j]
              }
            }
            prev.push(sumOffset)
            return prev
          }, [])

        this.foldTween
          .stop()
          .to(targetValue, 400)
          .onUpdate(function update () {
            self.$forceUpdate()
            self.foldOffsetValue = this
          })
          .start()
      }

      switch (e.type) {
        case 'click': {
          if (this.foldOffsetEnable[index]) {
            this.foldOffsetEnable[index] = false
            this.enableHoverToFold = false
          } else {
            this.foldOffsetEnable[this.foldOffsetEnable.indexOf(true)] = false
            this.foldOffsetEnable[index] = true
            // this.enableHoverToFold = true
          }

          foldTween.call(this)
          break
        }
        case 'mouseenter': {
          if ('ontouchstart' in window) {
            return
          }

          if (this.enableHoverToFold) {
            this.foldOffsetEnable[this.foldOffsetEnable.indexOf(true)] = false
            this.foldOffsetEnable[index] = true
          }

          const scale = [1, 1, 1, 1, 1, 1]
          scale[i] = 1.08
          scaleTween.call(this, scale)

          foldTween.call(this)
          break
        }
        case 'mouseleave': {
          this.enableHoverToFold = false
          const scale = [1, 1, 1, 1, 1, 1]
          scaleTween.call(this, scale)
          break
        }
        default :
      }
    },
    getTitleTransform (i) {
      return `translate3d(0,${this.foldOffsetValue[i]}em,0) scale(${this.titleScale[i]})`
    }
  },
  computed: {
    titleTransform: {
      get () {
        const strings = []
        for (let i = 0; i < 5; i += 1) {
          strings.push(`translate3d(0,${this.foldOffsetValue[i]}em,0) scale(${this.titleScale[i]})`)
        }
      }
    }
  },
  beforeRouteLeave (to, from, next) {
    next()
  },
  data () {
    return {
      perviousWidth: 0,
      rotateX: 0,
      rotateY: 0,
      foldOffsetEnable: [true, true, true, true, true, true],
      foldOffset: [0, 0, 0, 0, 0, 0],
      isCalculatingFoldOffset: true,
      foldOffsetValue: [0, 0, 0, 0, 0, 0],
      enableHoverToFold: false,
      titleScale: [1, 1, 1, 1, 1, 1]
    }
  }
}

</script>

<style lang="stylus" scoped>
.zoom-fade-enter-active, .zoom-fade-leave-active
  transition all .2s ease
  transform-origin top

.zoom-fade-enter, .zoom-fade-leave-active
  opacity 0
  transform scale(0)

.bg
  position fixed
  top 0
  width 100%
  left 0
  height 100%
  pointer-events none

@media (max-aspect-ratio: 4/3)
  .bg-1
    background url('../assets/hello_bg_mobile.png') no-repeat 100% 100%
    background-size 100% auto
    z-index 0
  .bg-2
    display none

@media (min-aspect-ratio: 4/3)
  .bg-1
    background url('../assets/golden_gate_bridge.jpg') no-repeat left center
    background-size cover
    z-index 0
    
  .bg-2
    background url('../assets/hello_bg_desk.png') no-repeat left center
    background-size auto 100%
    z-index 101
    display block

.main
  padding-top 120px
  display flex
  flex-direction column
  justify-content center
  padding-top 200px
  @media(min-aspect-ratio: 4/3)
    padding-top 240px
.content
  min-height 100%
  z-index 1000
  white-space nowrap
  position relative
  perspective 500
  perspective-origin 50% 50%
  @media(min-aspect-ratio: 4/3)
    padding-left 20%

.textrect-wrapper
  padding 0 3.2rem
  padding-bottom 240px
  width 32rem
  box-sizing border-box
  margin 0 auto
  font-size: 0.9rem;
  position relative
  text-align center
  perspective 500
  perspective-origin 50% 50%
  // transform-style preserve-3d
.textrect
  position absolute
  left 3.2rem
  top 0  
  &:first-child{
    position relative
    left 0
    top 0
  }
.text
  font-family "NeoSans"
  font-weight 700
  text-transform uppercase
  display inline-block

  &.dot
    background-clip text
    color transparent
    color white
    background-image url('../assets/mesh_texture.png')
    background-size 2px
.calculating-hide
  opacity 0
  transition none
.row
  position relative
  cursor pointer
  user-select none
  .blank
    position absolute
    width 100%
    left 0
    top 100%
    text-align left
    color rgba(black, 1)
    font-size 18px
    line-height 1.6em
    white-space pre-wrap
    padding-top 0.6em
    box-sizing border-box
    font-family "neoSans"
    font-weight bold
    background white
    padding 12px 18px
    border-radius 10px
    margin-top 1em
    font-family 'FFMark'
    cursor auto
    @media(min-aspect-ratio: 4/3)
      color rgba(white, 1)
      background black
  .title-wrapper
    position absolute
    left 0
    top 0
    &:first-child
      position static
    for row in 2 3 4 5 6 7 8 9 10
      &:nth-child({row})
        transform translateZ(-2px * (row - 1))
        opacity ((11 - row) / 24)
  .small
    font-size 1.6em
    color white
    // text-shadow 0 0 6px white
    @media(min-aspect-ratio: 4/3)
      color black
  &.hello
    margin-bottom 0.8em
    .blank
      background #222
      @media(max-aspect-ratio: 4/3)
        background white
    .text.color
      text-indent -0.05em
      font-size 8.8em
      color white
      line-height 0.8em
      @media(min-aspect-ratio: 4/3)
        color black
      // text-shadow 0 0 6px white
  &.wz
    margin-bottom 0.8em
    .blank
      background #ff5e5e
    .text.color
      font-size 3.3em
      color #FF1655
      // text-shadow 0 0 6px #FF1655
      // line-height 1.5em
  &.designer
    margin-bottom 0.5em
    .blank
      background #ffd214
    .text.color
      font-size 4.91em
      color #FFE200
      // text-shadow 0 0 6px #FFE200
      // line-height 1em
  &.developer
    margin-bottom 0.6em
    .blank
      background #59dc95
    .text.color
      font-size 3.8em
      color #44DB93
      // text-shadow 0 0 6px #44DB93
      // line-height 1.3em
  &.shanghai
    margin-bottom 0.7em
    .blank
      background #68c07d
    .text.color
      font-size 3.5em
      color #79C06C
      // text-shadow 0 0 6px #79C06C
      // line-height 1.1em
  &.contact
    .blank
      background-color #65b5ff
      cursor pointer
      transition 0.1s
      height 4.4em
      background-position center
      background-size 1.8em
      background-repeat no-repeat
      background-image url('../assets/lighting.svg')
      @media(min-aspect-ratio: 4/3)
        background-image url('../assets/lighting_white.svg')
      &:hover
        background-color #499bff
      &:active
        background-color #3f95ff
    .text.color
      font-size 3.25em
      color #5DC2FF
      // text-shadow 0 0 6px #79C06C
      // line-height 1.4em
</style>



