import * as THREE from 'three'
import dotMap from '@/assets/dot-white.png'

// const isWebGLAvailable = (function checkWebGLAvailable() {
//   try {
//     const canvas = document.createElement('canvas')
//     return !!(
//       window.WebGLRenderingContext
//       && (canvas.getContext('webgl')
//       || canvas.getContext('experimental-webgl'))
//     )
//   } catch (e) {
//     return false
//   }
// }())

const SEPARATION = 120
let AMOUNTX = 50
const AMOUNTY = 14
let particles
let particle
let count = 0
let scene
let camera
let requestId
let renderer
let customOption = {}
let updateCallback = function updateCallback () {}
const option = {}
let defOption = {}
let material

if (typeof window !== 'undefined') {
  defOption = {
    width: window.innerWidth,
    height: window.innerHeight,
    fov: 30
  }
}

function toScreenPosition (obj) {
  const vector = new THREE.Vector3()
  const canvas = renderer.domElement
  const { x, y, z } = obj
  vector.set(x, y, z)

  // map to normalized device coordinate (NDC) space
  vector.project(camera)

  // map to 2D screen space
  return {
    x: Math.round((vector.x + 1) * (canvas.width / 2)),
    y: Math.round((-vector.y + 1) * (canvas.height / 2))
  }
}

export default {
  init (container, _customOption) {
    customOption = _customOption
    Object.assign(option, defOption, customOption)
    AMOUNTX = Math.round(window.innerWidth / 10)
    if (AMOUNTX % 2 !== 0) {
      AMOUNTX += 1
    }
    scene = new THREE.Scene()
    scene.fog = new THREE.Fog(option.fogColor, 1000, 2800)
    const aspect = option.width / option.height
    camera = new THREE.PerspectiveCamera(option.fov, aspect, 1, 10000)
    particles = []
    const spriteMap = new THREE.TextureLoader().load(dotMap)
    material = new THREE.SpriteMaterial({
      map: spriteMap,
      color: option.dotColor,
      fog: true
    })

    let i = 0
    for (let ix = 0; ix < AMOUNTX; ix += 1) {
      for (let iy = 0; iy < AMOUNTY; iy += 1) {
        particles[i] = new THREE.Sprite(material)
        particle = particles[i]
        particle.position.x = (ix * SEPARATION) - (((AMOUNTX - 1) * SEPARATION) / 2)
        particle.position.z = (iy * SEPARATION) - (((AMOUNTY - 1) * SEPARATION) / 1)
        scene.add(particle)
        i += 1
      }
    }

    // var renderer = isWebGLAvailable ? new THREE.WebGLRenderer(): new THREE.CanvasRenderer()
    renderer = new THREE.WebGLRenderer({ alpha: true })
    renderer.setClearColor(0xffffff, 0)
    renderer.setPixelRatio(window.devicePixelRatio)
    renderer.setSize(option.width, option.height)
    container.appendChild(renderer.domElement)
    window.renderer = renderer

    function render () {
      camera.position.set(0, 240, 1000)
      camera.lookAt(new THREE.Vector3(0, 120, 0))
      let renderIndex = 0
      for (let ix = 0; ix < AMOUNTX; ix += 1) {
        for (let iy = 0; iy < AMOUNTY; iy += 1) {
          particle = particles[renderIndex]

          const waveX = Math.sin((ix + count) * 0.3)
          const waveY = Math.sin((iy + count) * 0.5)

          particle.position.y = (waveX + waveY) * 50

          const waveScale = ((waveX + 1 + waveY + 1) * 3) + 3
          // const waveScaleFade = (iy + 1) / AMOUNTY

          particle.scale.x = waveScale
          particle.scale.y = waveScale

          renderIndex += 1
        }
      }

      renderer.render(scene, camera)
      count += 0.1
      updateCallback()
    }

    function animate () {
      requestId = requestAnimationFrame(animate)
      render()
    }

    animate()
    window.addEventListener('resize', this.resize)
  },
  getCurveArray () {
    const curveArray = []
    for (let ix = 0; ix < AMOUNTX; ix += 1) {
      const curveParticle = particles[(ix * AMOUNTY) + (AMOUNTY - 3)]
      const { x, y } = toScreenPosition(curveParticle.position)
      curveArray.push([x, y])
    }
    return curveArray
  },
  destroy () {
    cancelAnimationFrame(requestId)
    scene = null
    renderer = null
    window.removeEventListener('resize', this.resize)
  },
  resize () {
    defOption.width = window.innerWidth
    defOption.height = window.innerHeight
    Object.assign(option, defOption, customOption)
    renderer.setSize(option.width, option.height)
    renderer.setPixelRatio(window.devicePixelRatio)
    const aspect = option.width / option.height
    camera = new THREE.PerspectiveCamera(option.fov, aspect, 1, 10000)
  },
  onUpdate (callback) {
    updateCallback = callback
  },
  changeDotColor (colorHex) {
    material.color.setHex(colorHex)
  },
  changeFogColor (colorHex) {
    scene.fog.color.setHex(colorHex)
  }
}



