import Vue from 'vue'
import Vuex from 'vuex'
import { fetchWorksData } from './api'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    works: null
  },
  mutations: {
    SET_WORKS_DATA: (state, data) => {
      state.works = data
    }
  },
  actions: {
    INIT: ({ dispatch }) => {
      dispatch('FETCH_WORKS_DATA')
    },
    FETCH_WORKS_DATA: ({ commit }) => {
      fetchWorksData()
        .then((res) => {
          commit('SET_WORKS_DATA', res.results)
        })
        .catch((err) => {
          console.log(err) //eslint-disable-line
          // commit('SET_WORKS_DATA', err)
        })
    }
  }
})

export default store



