import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/views/Hello'
import Works from '@/views/Works'
import Work from '@/views/Work'
import Contact from '@/views/Contact'

Vue.use(Router)

window.history.scrollRestoration = 'manual'

export default new Router({
  scrollBehavior (from, to, savedPosition) {
    return new Promise((resolve) => {
      setTimeout(() => {
        if (to.hash) {
          resolve({
            selector: to.hash
            // offset: { x: 0, y: 10 }
          })
        } else if (savedPosition) {
          resolve(savedPosition)
        } else {
          resolve({ x: 0, y: 0 })
        }
      }, 300)
    })
  },
  routes: [
    {
      path: '/',
      name: 'hello',
      component: Hello,
      meta: {
        tab: 0
      }
    },
    {
      path: '/works',
      name: 'works',
      component: Works,
      meta: {
        tab: 1
      }
    },
    {
      path: '/work/:slug',
      name: 'work',
      component: Work,
      meta: {
        tab: 1.1
      }
    },
    {
      path: '/preview',
      name: 'preview',
      component: Works,
      meta: {
        tab: 1.1
      }
    },
    {
      path: '/contact',
      name: 'contact',
      component: Contact,
      meta: {
        tab: 2
      }
    }
  ]
})



