<template lang="pug">
.main(:style="{backgroundColor: data && data.bg_color || null}")
  .article(v-if="data")
    .meta
      .title-line
        h1 {{data.project_name}}
      table
        tr
          td(width="44") Role
          td {{data.service}}
        tr
          td Year
          td {{data.year && data.year.split(',')[0]}}
        tr
          td Link
          td
            a(:href="data.external_link.url" target="_blank", :class="{empty: data.external_link.url}") {{data.external_link_text}}
    .body(:style="{opacity: routeTransitionFinish ? 1 : 0}")
      HtmlTransform(:template="getHtml")
  .work-footer(:style="{opacity: routeTransitionFinish ? 1 : 0}")
    .nav(v-if="works")
      .next-work(v-if="next")
        router-link.nav-link(:to=`{
          name: 'work',
          params: {
            slug: next.slug,
          } 
        }`) Next Project »
        .description(:style=`{
          textShadow: '-1px 0 ${data && data.bg_color || 'white'}, 1px 0 ${data && data.bg_color || 'white'}'
        }`) {{next.project_name}}
    WebsiteFooter

</template>

<script>
import Footer from '@/components/Footer'
import PrismicDOM from 'prismic-dom'
import ImageLoad from '@/components/ImageLoad'
import Vue from 'vue'

Vue.component('ImageLoad', ImageLoad)

const HtmlTransform = {
  functional: true,
  props: {
    template: String
  },
  components: {
    ImageLoad
  },
  render (h, ctx) {
    const template = ctx.props.template
    // const re = /<(img)[^>]*>/g
    // let match = '' //eslint-disable-line

    // while (match = re.exec(template)) { //eslint-disable-line
    //   template = template.replace(/<(img)[^>]*\/?>/, (_match, p1, p2) => {
    //     const newElement = _match.replace('img', 'ImageLoad')
    //     const slashIndex = newElement.length - 1
    //     return `${newElement.slice(0, slashIndex)}/>`
    //   })
    // }

    const dynComponent = {
      template: `<div>${template}</div>`
    }
    const component = dynComponent
    return h(component)
  }
}

export default {
  name: 'work',
  components: {
    WebsiteFooter: Footer,
    HtmlTransform
  },
  mounted () {
    this.init()
    setTimeout(() => {
      this.routeTransitionFinish = true
    }, 160)
  },
  methods: {
    init () {
      if (this.works) {
        const work = this.works.filter(item => item.data.slug === this.slug)
        if (work[0]) {
          this.thisIndex = this.works.indexOf(work[0])
          this.data = work[0].data
        }
      }
    }
  },
  computed: {
    works () {
      if (this.$route.name === 'preview') {
        const works = this.$store.state.works
        return [works[works.length - 1]]
      }

      return this.$store.state.works
    },
    slug () {
      return this.$route.params.slug
    },
    tags () {
      if (this.data && this.data.service) {
        return this.data.service.split(',').map(tag => tag.trim())
      }
      return []
    },
    prev () {
      if (!this.works || this.thisIndex === null) return null

      const thisIndex = this.thisIndex

      if (thisIndex === -1 || thisIndex === 0) {
        return null
      }

      return this.works[thisIndex - 1].data
    },
    next () {
      if (!this.works || this.thisIndex === null) return null

      const thisIndex = this.thisIndex

      if (thisIndex === -1) {
        return null
      }

      if (thisIndex === this.works.length - 1) {
        return this.works[0].data
      }

      return this.works[thisIndex + 1].data
    },
    getHtml () {
      function htmlSerializer (element, content) {
        if (element.type === 'image') {
          const size = JSON.stringify(element.raw.dimensions).replace(/"/g, '')
          return `<ImageLoad src="${element.raw.url}" :size="${size}" bgColor="transparent"/>`
        }

        return null
      }

      return PrismicDOM.RichText.asHtml(this.data.body, null, htmlSerializer)
    }
  },
  watch: {
    works () {
      this.init()
    }
  },
  data () {
    return {
      routeTransitionFinish: false,
      data: null,
      thisIndex: null
    }
  }
}

</script>

<style lang="stylus" scoped>
.main
  align-items center
  padding-top 260px
  padding-bottom 160px
  flex-direction column
  flex none
  min-height 100%
  position relative
  @media(max-aspect-ratio: 4/3)
    padding-top 240px
.article
  min-height 100%
  position relative
  // max-width 1000px
  width 100%
  margin 0 auto
  .meta
    padding 0 12vh
    @media(max-aspect-ratio: 4/3)
      padding 0 4rem
  h1
    font-family NeoSans
    font-size 24px
    line-height 32px
    display inline

  >>> img 
    width 100%
    vertical-align bottom
  img.cover
    margin-top 24px

  table
    font-size 13px
    border-collapse collapse 
    color #808080
    margin-top 16px
    td
      vertical-align text-top
    a
      text-decoration none
      color inherit
      transition .2s ease
      &:hover
        color black
        text-decoration underline

.body
  margin-top 70px
  margin-bottom 100px
  transition: 0.3s ease-in
  @media(max-width: 414px)
    margin-top 30px
  >>> p
    margin 0

.work-footer
  margin auto
  padding 0 88px
  padding-top 80px
  margin-bottom 100px
  bottom 0
  position absolute
  width 100%
  left 0
  right 0
  @media(max-aspect-ratio: 4/3)
    padding 0 4rem
.nav
  .nav-link
    text-decoration none
    font-weight bold
    font-family NeoSans
    font-size 24px
    color inherit
    &:hover
      text-decoration underline
  .description
    font-size 13px
    color #808080
    margin-top 6px

.main >>> .footer
  right 88px
  width auto
  height auto
  line-height inherit
  @media(max-aspect-ratio: 4/3)
    right auto
    position relative
    text-align left
    bottom -60px
</style>



