export function fetchWorksData () {
  return Prismic.getApi(apiEndpoint).then(api => api.query(
    Prismic.Predicates.at('document.type', 'work'),
    { orderings: '[my.work.year desc]' }
  ))
}

export function fetchWorkData (slug) {
  return Prismic.getApi(apiEndpoint)
    .then(api =>
      api.query(Prismic.Predicates.any('my.work.slug', [slug]))
    )
}



