<template lang="pug">
  .wrapper(:style="bgColor ? {backgroundColor: bgColor} : null")
    .inner-wrapper(:style="calcRatioStyle()")
      transition(name="fade")
        img(:src="src" v-if="loaded")
      Spinner.spin(:show="!loaded" v-if="calcRatioStyle()")
</template>

<script>
import Spinner from '@/components/Spinner'

export default {
  name: 'image-load',
  components: {
    Spinner
  },
  props: {
    src: String,
    size: Object,
    bgColor: String
  },
  methods: {
    load () {
      if (!this.src) return

      const img = new Image()
      img.onload = () => {
        this.loaded = true
      }
      img.src = this.src

      if (!this.size) {
        const poll = setInterval(() => {
          if (img.naturalWidth) {
            clearInterval(poll)
            this.loadedRatioStyle = {
              paddingBottom: `${(img.naturalHeight / img.naturalWidth) * 100}%`
            }
            this.$forceUpdate()
          }
        }, 10)
      }
    },
    calcRatioStyle () {
      if (this.size) {
        const { height, width } = this.size
        return {
          paddingBottom: `${(height / width) * 100}%`
        }
      }

      return this.loadedRatioStyle
    }
  },
  mounted () {
    this.load()
  },
  watch: {
    src () {
      this.load()
    }
  },
  data () {
    return {
      loaded: false,
      loadedRatioStyle: null
    }
  }
}
</script>

<style lang="stylus">
.wrapper
  background #eee
  position relative
.inner-wrapper
  height 0
.spin
  position absolute
  left 0
  top 0
  bottom 0
  right 0
  margin auto
img
  width 100%
  &.img-pre-size
    position absolute
    top 0
    left 0
</style>


