<script>

/* eslint-disable*/
class Magnification {
  constructor (n, r) {
    this.n = n
    this.r = r
    this.range = this.range.bind(this)
    this.integral = this.integral.bind(this)
    this.integralRange = this.integralRange.bind(this)
  }

  range (i) {
    const r = this.r

    let left = -r + i
    let right = left + 1

    if (left < -r) {
      left = -r
    }
    if (right > r) {
      right = r
    }
    return [left, right]
  }

  integral (x) {
    const t = this.r * 2
    const c = (2 * Math.PI) / t
    // integration of (cos(c * x) + 1) * 1 / 2
    // if (x < -t/2 || x > t/2) return 0
    return ((1 / (2 * c)) * Math.sin(c * x)) + (x / 2)
  }

  integralRange ([left, right]) {
    return (this.integral(right) - this.integral(left))
  }

  calc (offset, scale) {
    const result = []
    const magicNumber = (this.r < 2 ? 0 : 1) + ((this.r % 2) / 2)
    for (let i = -offset; i < this.n - offset; i += 1) {
      let value = this.integralRange(this.range((i + magicNumber) + (this.r / 2)))
      if (value < 0) {
        value = 0
      } else if (value > 1) {
        value = 1
      }
      result.push(value * scale)
    }

    return result
  }
}

function lowPass (current, target) {
  const ALPHA = 0.9
  const result = (ALPHA * current) + ((1.0 - ALPHA) * target)
  if (result < 0) return 0
  else if (target !== 0 && result > target) return target
  return result
}

export default {
  abstract: true,
  name: 'magnification',
  components: {
  },
  methods: {
    mouseEnter () {
      cancelAnimationFrame(this.springId)

      function spring () {
        if (Math.abs(this.scale - this.targetScale) < 0.001) {
          this.scale = this.targetScale
          cancelAnimationFrame(this.springId)
          this.springId = null
          return
        }
        this.scale = lowPass(this.scale, this.targetScale)
        this.springId = requestAnimationFrame(() => {
          spring.call(this)
        })
      }

      spring.call(this)
    },
    mouseLeave () {
      cancelAnimationFrame(this.springId)
      function spring () {
        if (this.scale < 0.001) {
          this.scale = 0
          cancelAnimationFrame(this.springId)
          this.springId = null
          return
        }
        this.scale = lowPass(this.scale, 0)
        this.springId = requestAnimationFrame(() => {
          spring.call(this)
        })
      }

      spring.call(this)
    },
    mousemove (e) {
      this.x = e.x
      this.w = e.target.offsetWidth
      this.update(e.x)
    },
    update (x) {
      const mouseX = x || this.x
      const mouseXOfContainer = mouseX - this.containerRect.x
      const w = this.w
      const offset = mouseXOfContainer / w
      const scales = this.magnify.calc(offset, this.scale)
      const maxScaleSum = this.magnify.calc(this.total/2, this.scale).reduce((acc, next) => {
        return acc + next
      }, 0)
      const leftScaleSum = this.magnify.calc(0, this.scale).reduce((acc, next) => {
        return acc + next
      }, 0)
      this.scales = scales

      let translates = [...Array(this.total)].map(value => 0)
      for ( let i = 0; i < translates.length; i += 1) {
        if (i === 0) continue
        translates[i] += translates[i - 1] + (scales[i - 1] / 2) + (scales[i] / 2)
      }
      const scaleSum = scales.reduce((acc, next, i) => {
        return acc + next
      }, 0)

      if (offset >= this.total/2){
        translates = translates.map(value => value - maxScaleSum / 2)
      } else {
        const wholeTranslate = maxScaleSum / 2 - translates[translates.length - 1]
        translates = translates.map(value => value + wholeTranslate)
      }
      
      this.translates = translates
    }
  },
  created () {
    this.magnify = new Magnification(this.total, this.range, this.scale)
  },
  mounted () {
    this.container = this.$refs.container
  },
  computed: {
    containerRect () {
      return this.container.getBoundingClientRect()
    }
  },
  watch: {
    scale () {
      this.update()
    }
  },
  data () {
    const total = this.$slots.default.length
    return {
      range: 1,
      total,
      targetScale: 0.5,
      scale: 0,
      scales: [...Array(total)].map(() => 0),
      translates: [...Array(total)].map(() => 0)
    }
  },
  render () {
    return (
      <ul
        ref='container'
        onMouseenter={this.mouseEnter}
        onMouseleave={this.mouseLeave}
      >
        {
          [...Array(this.total)].map((n, i) => (
            <li
              style={{ transform: `translate3d(${this.translates[i]*100}%,0,0) scale(${this.scales[i] + 1}) ` }}
              onMousemove={this.mousemove}
            >
              {this.$slots.default[i]}
            </li>
          ))
        }
      </ul>
    )
  }
}

</script>

<style lang="stylus" scoped>
ul
  list-style-type none
  display inline-block
  padding 0  
li
  // border 1px solid black
  display inline-block
  height 44px
  width 64px
</style>


